<template>
  <!--  -->
  <div id="app">
    <h3></h3>

    <el-table
    :data="tableData"
    border
    stripe
    style="width: 100%">
    <el-table-column
      prop="title"
      label=""
      align = "center"
      width="300">
    </el-table-column>
    <el-table-column
      prop="teacher"
      label=""
      align = "center"
      width="200">
    </el-table-column>
    <el-table-column
      prop="time"
      label=""
      align = "center"
      width="300">
    </el-table-column>
    <el-table-column
      label=""
      align = "center"
      width="200">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row, scope.$index)" type="text" size="medium"></el-button>
      </template>
    </el-table-column>
  </el-table>
  </div>
</template>



<script>
export default {
  name: "classes",
  data() {
    return {
      tableData: [
        { title: "1", teacher: "1", time: "1", msgid: "1"},
        { title: "2", teacher: "2", time: "2", msgid: "2"},
        { title: "3", teacher: "3", time: "3", msgid: "3"},
        { title: "4", teacher: "4", time: "4", msgid: "4"}
      ],
      keywords: ""
    };
  },
  methods: {
    add() {
      // vuedataVue
      // 
      this.list.push({ id: this.id, name: this.name, ctime: new Date() });
    },

    del(id) {
      // Id
      // this.list.some((item, i) => {
      //     if (item.id == id) {
      //         this.list.splice(i, 1)
      //         //  some return true 
      //         return true
      //     }
      // })
      /*
      let index = this.list.findIndex(item => {
        if (item.id == id) {
          return true;
        }
      });

      this.list.splice(index, 1);
      */
    },

    handleClick(row, idx) {
      console.log(row)
      console.log(idx)
      var url = this.$route.path+'/'+this.tableData[idx].msgid;
      this.$router.push(url);
    },

    search(keywords) {
      var newList = [];
      // this.list.forEach(item => {
      //     if (item.name.indexOf(keywords) != -1) {
      //         newList.push(item)
      //     }
      // });
      // return newList;

      // forEach some fliter findIndex 
      // 
      return this.list.filter(item => {
        //:ES6 String.prototype.includes("")
        // truefalse
        if (item.name.includes(keywords)) {
          return item;
        }
      });
    },
    getinfo: function () {
      var _this = this
      $.ajax({
        type: 'get',
        url: '/api/hwlist_ajax',
        contentType: 'application/json; charset=utf-8',
        data: JSON.stringify({classid: this.$route.params.classid}),
        dataType: 'json',
        success: function (data) {
          _this.tableData = data.tableData;
        },
        error: function (data) {
          _this.$notify({
            title: '',
            message: ''
          })
        }
      })
    }
  },
  created () {
    //this.getinfo()
  }
};
</script>


