<template>
  <el-dialog title=""
             :visible.sync="visible"
             :close-on-press-escape="false"
             :close-on-click-modal="false"
             :before-close="handleClose">
    <el-form :model="form">
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="form.number" placeholder="" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select v-model="form.type" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-radio-group v-model="form.content">
          <el-radio label="1"></el-radio>
          <el-radio label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input v-model="form.remark" type="textarea" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-date-picker v-model="form.createTime" type="date" placeholder="" disabled></el-date-picker>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-date-picker v-model="form.activeTime" type="date" placeholder=""></el-date-picker>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="handleClose"></el-button>
      <el-button type="primary" @click.native="editFormSubmit"></el-button>
    </div>
  </el-dialog>
</template>
<script>
  export default {
    props: {
      visible: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        form: {
          number: '',
          type: '',
          content: '1',
          remark: '',
          createTime: new Date(),
          activeTime: ''
        },
        formLabelWidth: '120px',
      }
    },
    methods: {
      editFormSubmit() {
        this.$message.success("");
      },
      // 
      handleClose() {
        this.$emit('update:visible', false);
      }
    }
  }
</script>



