<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <br>
    <h2>Essential Links</h2>
    <el-select v-model="value" placeholder="">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  <br>
  <br>
  <el-row>
    <el-button></el-button>
    <el-button type="primary"></el-button>
    <el-button type="success"></el-button>
    <el-button type="info"></el-button>
    <el-button type="warning"></el-button>
    <el-button type="danger"></el-button>
  </el-row>
  </div>
</template>

<script>

export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to App',
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value: ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



