<template>
  <div class="warp-main">
    <el-row :span="24" v-loading="loading" element-loading-text="">
      <el-col :span="24" class="toolbar" style="padding-bottom: 0;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.name" placeholder="" auto-complete="off" @keyup.enter.native="fetchData"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" v-on:click="fetchData"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-table ref="multipleTable" :data="tableData" border style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="60"></el-table-column>
        <el-table-column prop="eNumber" label="" width="180" sortable></el-table-column>
        <el-table-column prop="eName" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="eIndustry" label="" width="180"></el-table-column>
        <el-table-column prop="eRange" label="" width="180"></el-table-column>
        <el-table-column prop="eModel" label="" width="180"></el-table-column>
        <el-table-column prop="createTime" label="" width="180"></el-table-column>
        <el-table-column prop="updateTime" label="" width="180"></el-table-column>
        <el-table-column prop="recordStatus" label="" width="180"></el-table-column>
      </el-table>
      <!--<div style="margin-top: 20px">
        <el-button @click="toggleSelection([tableData3[1], tableData3[2]])"></el-button>
        <el-button @click="toggleSelection()"></el-button>
      </div>-->
      <el-col :span="24" class="toolbar">
        <el-pagination background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 50, 100, 200]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  //import API from '../../api/api_enterprise'
  export default {
    data() {
      return {
        loading: false,
        keyword: "",
        total: 5,
        currentPage: 1,
        pageSize: 10,
        tableData: [{
          eNumber: 'A10001',
          eName: 'YE',
          eIndustry: '',
          eRange: '',
          eModel: '',
          createTime: '2017-03-27',
          updateTime: '2016-03-27',
          recordStatus: '1'
        }],
        multipleSelection: [],
        filters: {
          name: ''
        }
      }
    },
    created: function(){
      // 
      //  data  observed 
      //this.fetchData();  //

    },
    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(function(row) {
            this.$refs.multipleTable.toggleRowSelection(row);
        });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      fetchData(){ //
        let that = this;
        let params = {
          curr: that.currentPage,
          pageSize: that.pageSize,
          keywords: that.filters.name
        };
        that.loading = true;
        /*
        API.findList(params).then(function(result){
          that.loading = false;
          that.total = result.count;
          that.currentPage = result.curr;
          that.tableData = result.data;
        }).catch(function (error) {
          that.loading = false;
          console.log(error);
        });
        */
      },
      handleSizeChange(val){
        this.pageSize = val;
        this.currentPage = 1;
        this.fetchData();
//        console.log(` ${val} `);
      },
      handleCurrentChange(val){
        this.currentPage = val;
        this.fetchData();
//        console.log(`: ${val}`);
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    }
  }
</script>
<style>
  .el-table th {
    text-align: center;
  }

  .el-table tbody tr td:first-child {
    text-align: center;
  }
</style>



