<template>
  <div class="warp-main" style="overflow: hidden; padding: 10px;">
    <p style="margin-bottom: 20px; font-weight: 500;"></p>
    <el-form ref="form" :model="form" label-width="110px">
      <el-col :span="12">
        <el-form-item label="">
          <el-input v-model="form.number" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.industry" placeholder="">
            <el-option label="" value="-1"></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="IT" value="2"></el-option>
            <el-option label="" value="3"></el-option>
            <el-option label="" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-checkbox-group v-model="form.range">
            <el-checkbox label="" name="range"></el-checkbox>
            <el-checkbox label="" name="range"></el-checkbox>
            <el-checkbox label="" name="range"></el-checkbox>
            <el-checkbox label="" name="range"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item label="">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.model">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model="form.regDate" type="date" placeholder=""></el-date-picker>
        </el-form-item>
      </el-col>
      <el-col :span="24">
        <el-button type="primary" icon="el-icon-edit"></el-button>
      </el-col>
    </el-form>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        msg:'',
        form: {
          number: '',
          name: '',
          industry: '',
          model: '',
          range: [],
          regDate: ''
        }
      };
    }
  }
</script>



